.edgtf-stock-photography-single-holder {
    &.edgtf-stock-photography-single-standard {

        .edgtf-sps-content-holder {
            text-align: left;
        }

        .edgtf-sps-author-description {
            margin-bottom: 30px;
            .edgtf-author-description-image {
                width: 73px;
                line-height: 0;
                margin-bottom: 24px;
            }
        }

        .edgtf-sps-meta-holder {
            margin-bottom: 38px;
        }

        .edgtf-sps-info-holder {
            .edgtf-sps-info-item {
                margin-bottom: 15px;
            }
            .edgtf-sps-info-label {
                font-size: 12px;
                color: #000;
                font-weight: 500;
                letter-spacing: 0.08em;
                text-transform: uppercase;
            }
        }

        .edgtf-sps-tags {
            margin-bottom: 15px;
            .edgtf-sps-tags-label {
                font-size: 12px;
                color: #000;
                font-weight: 500;
                letter-spacing: 0.08em;
                text-transform: uppercase;
            }
            .edgtf-sps-tag {
                text-transform: lowercase;
                color: #878787;
                &:before {
                    content: '#';
                }
            }
        }

        .edgtf-sps-date-info {
            margin-bottom: 15px;
            .edgtf-sps-date-label {
                font-size: 12px;
                color: #000;
                font-weight: 500;
                letter-spacing: 0.08em;
                text-transform: uppercase;
            }
        }

        .edgtf-sps-favorites-info {
            margin-bottom: 15px;
            .edgtf-photography-favorites-label {
                font-size: 12px;
                color: #000;
                font-weight: 500;
                letter-spacing: 0.08em;
                text-transform: uppercase;
            }
        }

        .edgtf-sps-downloads-info {
            .edgtf-photography-downloads-label {
                font-size: 12px;
                color: #000;
                font-weight: 500;
                letter-spacing: 0.08em;
                text-transform: uppercase;
            }
        }

        .edgtf-sps-licence-info {
            margin-bottom: 37px;
            font-size: 18px;
            line-height: 32px;
        }

        .edgtf-sps-action {
            a,
            button {
                min-width: 225px;
                text-align: center;
            }
            .edgtf-buy-item-form,
            .edgtf-stock-photography-cart-holder,
            .edgtf-stock-photography-download-holder {
                margin-bottom: 15px;
            }
        }

        .edgtf-sp-social-share {
            margin-top: 44px;
            .edgtf-social-share-holder.edgtf-list {
                .edgtf-social-title {
                    font-size: 12px;
                    color: #000;
                    font-weight: 500;
                    letter-spacing: 0.08em;
                    text-transform: uppercase;
                    padding-right: 2px;
                }
                li {
                    &.edgtf-facebook-share {
                        a {
                            color: #3b5998;
                        }
                    }
                    &.edgtf-twitter-share {
                        a {
                            color: #55acee;
                        }
                    }
                    &.edgtf-pinterest-share {
                        a {
                            color: #cb2027;
                        }
                    }
                }
            }
        }

        .edgtf-stock-photography-section-bottom {
            margin-top: 88px;
        }
    }
}