/* ==========================================================================
   Stock Photography Slider shortcode style - begin
   ========================================================================== */

.edgtf-stock-photography-slider-holder {
    @include edgtfRelativeHolderLayout();

    .edgtf-sp-inner {
        margin: 0 !important;
    }

    article {
        width: auto !important;
        padding: 0 !important;
        margin: 0 !important;
        float: none !important;
    }

    .edgtf-stock-photography-list-holder {

        &.edgtf-nav-light-skin {

            .owl-nav {

                .owl-prev,
                .owl-next {
                    color: $header-light-color;
                    
                    &:hover {
                        color: $header-light-hover-color;
                    }
                }
            }
        }

        &.edgtf-nav-dark-skin {

            .owl-nav {

                .owl-prev,
                .owl-next {
                    color: $header-dark-color;
                    
                    &:hover {
                        color: $header-dark-hover-color;
                    }
                }
            }
        }

        &.edgtf-pag-light-skin {

            .owl-dots {

                .owl-dot {

                    span {
                        background-color: rgba($header-light-color, .2);
                    }

                    &.active,
                    &:hover {

                        span {
                            background-color: $header-light-hover-color;
                        }
                    }
                }
            }
        }

        &.edgtf-pag-dark-skin {

            .owl-dots {

                .owl-dot {

                    span {
                        background-color: rgba($header-dark-color, .2);
                    }

                    &.active,
                    &:hover {

                        span {
                            background-color: $header-dark-hover-color;
                        }
                    }
                }
            }
        }

        &.edgtf-pag-on-slider {

            .owl-nav {

                .owl-prev,
                .owl-next {
                    @include edgtfTransform(translateY(-50%));
                }
            }

            .owl-dots {
                position: absolute;
                left: 0;
                bottom: 20px;
                width: 100%;
                margin: 0;
            }
        }
    }
}
/* ==========================================================================
   Stock Photography Slider shortcode style - end
   ========================================================================== */