/* ==========================================================================
   Stock Photography Item Layout - Standard style - begin
   ========================================================================== */

.edgtf-stock-photography-list-holder {

    &.edgtf-sp-standard {

        article {
            &:hover {

                .edgtf-spi-text-holder {
                    opacity: 1;
                }
            }

            .edgtf-spi-text-holder {
                @include edgtfAbsoluteHolderLayout();
                padding: 20px;
                background-color: rgba(#fff, 0.83);
                opacity: 0;
                text-align: center;
                box-sizing: border-box;
                @include edgtfTransition(opacity .2s ease-in-out);
            }

            .edgtf-spi-cart,
            .edgtf-spi-favorites,
            .edgtf-spi-zoom {
                position: relative;
                z-index: 99;
                display: inline-block;
                padding-left: 25px;

                a {
                    font-size: 27px;
                    color: #000;

                    .fa-download {
                        vertical-align: middle;
                    }
                }
            }
            .edgtf-spi-cart {
                padding-left: 0;
            }
        }
    }
}
/* ==========================================================================
   Stock Photography Item Layout - Standard style - end
   ========================================================================== */