/* ==========================================================================
   Proofing Gallery Single page style - begin
   ========================================================================== */

.edgtf-proofing-gallery-single-holder {
	@include edgtfRelativeHolderLayout();

	.edgtf-pgs-gallery-holder {
		@include edgtfRelativeHolderLayout();
		padding: 35px 0 0;

		.edgtf-pgs-gallery-image {
			@include edgtfRelativeHolderLayout();
			box-sizing: border-box;
			float: left;

			.edgtf-pgs-gallery-image-inner {
				box-sizing: border-box;
				border: 1px solid $default-border-color;

			}

			img {
				vertical-align: middle;
			}

			.edgtf-pgs-gallery-image-info {

				padding: 16px 54px;
				box-sizing: border-box;
				border-top: 1px solid $default-border-color;
				position: relative;
				text-align: center;

				.edgtf-pgs-gallery-image-info-pretty-photo {
					position: absolute;
					left: 0;
					width: 54px;
					text-align: center;
					top: 50%;
					@include edgtfTransform(translateY(-50%));
					color: $default-text-color;
					font-size: 14px;
				}

				.edgtf-pgs-image-action-icon {
					position: absolute;
					right: 0;
					width: 54px;
					text-align: center;
					top: 50%;
					@include edgtfTransform(translateY(-50%));
					cursor: pointer;

					@include keyframes(edgtf-blink) {
						0%,100% {
							opacity: 1;
						}

						50% {
							opacity: 0;
						}
					}

					&.edgtf-blink {
						@include edgtfAnimation(edgtf-blink 1s infinite ease);
					}
				}
				.edgtf-pgs-gallery-image-id {
					color: $first-main-color;
					letter-spacing: 1.5px;
					font-weight: 400;
				}

			}

			.edgtf-pgs-reject-icon {
				display: none;
			}

			&.proofing-gallery-image-approved {
				.edgtf-pgs-gallery-image-inner {
					border: 2px solid $first-main-color;

				}
				.edgtf-pgs-gallery-image-info {
					border-top: 1px solid $first-main-color;
					background-color: $first-main-color;

					.edgtf-pgs-gallery-image-id {
						color: #fff;
					}

				}

				.edgtf-pgs-reject-icon {
					display: inline-block;
				}
				.edgtf-pgs-approve-icon {
					display: none;
				}

			}
		}
	}

	.edgtf-pgs-meta-holder {
		text-align: center;

		.edgtf-pgs-meta-inner {
			display: inline-block;
			vertical-align: middle;

			> div {
				float: left;
				padding: 0 7px;

				span {
					color: $first-main-color;
				}
			}
		}
	}


	&.edgtf-proofing-gallery-light {

		.edgtf-pgs-gallery-holder {

			.edgtf-pgs-gallery-image {

				.edgtf-pgs-gallery-image-inner {
					border: 1px solid #8b8b8b;

				}


				.edgtf-pgs-gallery-image-info {

					border-top: 1px solid #8b8b8b;

						.edgtf-pgs-gallery-image-id {
							color: #fff;
						}

				}

				&.proofing-gallery-image-approved {
					.edgtf-pgs-gallery-image-inner {
						border: 2px solid #fff;

					}
					.edgtf-pgs-gallery-image-info {
						border-top: 1px solid #fff;
						background-color: #fff;

						.edgtf-pgs-gallery-image-id {
							color: $first-main-color;
						}

					}

				}
			}
		}

		.edgtf-pgs-meta-holder {

			.edgtf-pgs-meta-inner {
				> div {
					span {
						color: #fff;
					}
				}
			}
		}


		.edgtf-pgs-gallery-filter-holder {

			.edgtf-pgs-gallery-filter-inner {

				li {
					&.edgtf-pgs-filter-current {
						color: #fff;
					}
				}
			}
		}

	}

}

/* ==========================================================================
   Proofing Gallery Single page style - end
   ========================================================================== */

/* ==========================================================================
   Proofing Gallery Single page specific style for types - begin
   ========================================================================== */

.edgtf-proofing-gallery-single-holder {

	.edgtf-pgs-gallery-holder {

		&.edgtf-pgs-gallery-masonry {

			.edgtf-pgs-gallery-image,
			.edgtf-pgs-grid-sizer {
				width: 25%;
			}

			.edgtf-pgs-grid-gutter {
				width: 0;
			}

			/***** Columns Layout - begin *****/

			$columns_number: ('two', 'three', 'four');

			@for $i from 0 to length($columns_number) {
				&.edgtf-pgs-gallery-#{nth($columns_number,$i+1)}-columns {
					$column_width: 100%/($i+2);

					.edgtf-pgs-gallery-image,
					.edgtf-pgs-grid-sizer {
						width: $column_width;
					}

				}
			}

			/***** Columns Layout - end *****/
		}
	}
}
/* ==========================================================================
   Proofing Gallery Single page specific style for types - end
   ========================================================================== */

/* ==========================================================================
   Proofing Gallery filter style - begin
   ========================================================================== */

.edgtf-pgs-gallery-filter-holder {
	position: relative;
	width: 100%;
	vertical-align: middle;
	text-align: center;
	padding: 50px 0;

	.edgtf-pgs-gallery-filter-inner {
		display: inline-block;
		vertical-align: middle;

		ul {
			margin: 0;
			padding: 0;
		}

		li {
			display: inline-block;
			vertical-align: middle;
			margin: 0 10px;
			cursor: pointer;
			font-weight: 600;
			text-transform: uppercase;
			letter-spacing: 2.5px;

			&.edgtf-pgs-filter-current {
				color: $first-main-color;
			}
		}
	}

	.edgtf-pgs-gallery-download-holder {
		position: absolute;
		top: 50%;
		@include edgtfTransform(translateY(-50%));
		right: 0;
		left: auto;
		.edgtf-btn-dark:hover {
			background-color: $first-main-color;
			border: 1px solid $first-main-color;
			color: #fff;
		}
	}
}
/* ==========================================================================
   Proofing Gallery single fitler style - end
   ========================================================================== */

/* ==========================================================================
   Proofing Gallery single password protected style - start
   ========================================================================== */

.edgtf-content .edgtf-content-inner > .edgtf-full-width.edgtf-pgs-full-width-no-padding > .edgtf-full-width-inner {
	padding: 0 !important;
}

.edgtf-password-protected-holder {

	text-align: center;
	width: 100%;
	display: table;
	table-layout: fixed;


	.edgtf-password-protected-holder-inner {
		display: table-cell;
		vertical-align: middle;
		padding: 50px 10px;
		box-sizing: border-box;
	}

	.edgtf-password-protected-subtitle {
		margin-top: 12px;
	}

	.edgtf-password-protected-padlock {
		width: 111px;
		height: 150px;
		display: inline-block;
		background-image: url(../img/proofing-gallery-padlock-dark.png);
		background-repeat: no-repeat;
		background-size: cover;
		margin-bottom: 23px;
	}

	.edgtf-password-protected-fields {
		border: 1px solid #4a4c52;
		padding: 0 55px;
		text-align: center;
		position: relative;
		width: 330px;
		max-width: 100%;
		box-sizing: border-box;
		display: inline-block;
		margin: 28px 0 0 0;
	}

	input[type='password']{
		background-color: transparent;
		border: none;
		text-align: center;
		padding: 14px 0;
		outline: none;
		font-family: Poppins, serif;
		font-size: 11px;
		letter-spacing: 2px;
		text-transform: uppercase;
		color: #64686e;
		width: 100%;
	}

	::-webkit-input-placeholder{
		color: #64686e;
		opacity: 1;
	}

	:-moz-placeholder {
		color: #64686e;
		opacity: 1;
	}
	::-moz-placeholder {
		color: #64686e;
		opacity: 1;
	}
	:-ms-input-placeholder{
		color: #64686e;
		opacity: 1;
	}

	input[type='submit']{
		position: absolute;
		right: 0;
		top: 0;
		height: 100%;
		width: 50px;
		font-family: FontAwesome, serif;
		font-size: 21px;
		background-color: transparent;
		border: none;
		padding: 0;
		cursor: pointer;
	}

	&.edgtf-password-protected-light{
		.edgtf-password-protected-padlock {
			background-image: url(../img/proofing-gallery-padlock-light.png);
		}
		.edgtf-password-protected-title,
		.edgtf-password-protected-subtitle {
			color: #fff;
		}
		input[type='submit']{
			color: #fff;
		}
		.edgtf-password-protected-fields {
			border-color: #a9a9a9;
		}
	}

}


/* ==========================================================================
   Proofing Gallery single password protected style - end
   ========================================================================== */