.edgtf-core-profile-favorites-holder {
    .edgtf-core-profile-favorite-item {
        @include edgtfTableLayout();
    }
    .edgtf-core-profile-favorite-item-image {
        @include edgtfTableCellLayout();
        width: 100px;
    }
    .edgtf-core-profile-favorite-item-title {
        @include edgtfTableCellLayout();
        padding-left: 15px;
    }
    .edgtf-spi-cart {
        display: inline-block;
    }
}