/* ==========================================================================
   Stock Photography shortcode style - begin
   ========================================================================== */

.edgtf-stock-photography-list-holder {
    @include edgtfRelativeHolderLayout();

    /***** Article Global Style - begin *****/

    article {
        @include edgtfRelativeHolderLayout();
        box-sizing: border-box;

        .touch & {
            cursor: pointer;
        }

        .edgtf-sp-item-inner {
            @include edgtfRelativeHolderLayout();
            overflow: hidden;
        }

        .edgtf-spi-image {
            @include edgtfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }
        }

        .edgtf-spi-link {
            @include edgtfAbsoluteHolderLayout();
        }

        .edgtf-spi-text-wrapper {
            @include edgtfTableLayout();
        }

        .edgtf-spi-text {
            @include edgtfTableCellLayout();
        }
    }

    /***** Article Global Style - end *****/

    /***** Specific Global Style - begin *****/

    &.edgtf-sp-has-filter {

        article {

            .edgtf-sp-inner {
                overflow: hidden;
            }
        }
    }

    /***** Specific Global Style - end *****/

    /***** Stock Photography Types - begin *****/

    &.edgtf-sp-gallery {

        &:not(.edgtf-sp-one-column) {

            article {
                float: left;
            }
        }

        $gallery_columns_number: ('two', 'three', 'four', 'five');

        @for $i from 0 to length($gallery_columns_number) {
            &.edgtf-sp-#{nth($gallery_columns_number,$i+1)}-columns {

                article {
                    width: 100% / ($i+2);
                }

                @if ($i > 1) { // set different break point for four and five columns
                    @media only screen and (min-width: $laptop-landscape-plus-pixel) {

                        article {

                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                } @else {
                    @media only screen and (min-width: $ipad-landscape-plus-pixel) {

                        article {

                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }

    &.edgtf-sp-masonry {

        .edgtf-sp-inner {
            opacity: 0;
        }

        article,
        .edgtf-sp-grid-sizer {
            width: 100%;
        }

        .edgtf-sp-grid-gutter {
            width: 0;
        }

        $masonry_columns_number: ('two', 'three', 'four', 'five');

        @for $i from 0 to length($masonry_columns_number) {
            &.edgtf-sp-#{nth($masonry_columns_number,$i+1)}-columns {
                $column_width: 100%/($i+2);

                article,
                .edgtf-sp-grid-sizer {
                    width: $column_width;
                }

                article {

                    &.edgtf-sp-masonry-large-width,
                    &.edgtf-sp-masonry-large-width-height {
                        width: $column_width * 2;
                    }
                }
            }
        }

        &.edgtf-sp-images-fixed {

            article {

                .edgtf-sp-item-inner {
                    height: 100%;
                }

                .edgtf-spi-image {
                    height: 100%;

                    img {
                        height: 100%;
                    }
                }
            }
        }
    }

    /*** Justified gallery start styles ***/

    &.edgtf-sp-justified-gallery {

        opacity: 0;
        height: auto!important;

        .edgtf-sp-inner {
            margin: 0 !important;
            width: 100%;
            position: relative;
            overflow: hidden;
        }

        article {
            position: absolute;
            display: inline-block;
            padding: 0 !important;
            margin: 0 !important;
            width: auto;

            .edgtf-item-image-holder {
                height: 100%;
                overflow: hidden;
            }

            .edgtf-spi-image {
                height: 100%;
                display: block;
                width: auto;
                overflow: hidden;
                img {
                    width: 100%;
                    height: 100%;
                    vertical-align: middle;
                    max-height: 100%;
                }
            }
        }

    }

    /***** Stock Photography Types - end *****/

    /***** Additional Features - begin *****/

    &.edgtf-sp-has-animation {

        article {
            opacity: 0;
            @include edgtfTransform(translateY(80px));
            @include edgtfTransition(opacity .8s cubic-bezier(0.34, 0.52, 0.57, 1.04), transform .8s cubic-bezier(0.34, 0.52, 0.57, 1.04));

            &.edgtf-item-show {
                opacity: 1;
                @include edgtfTransform(translateY(0));

                &.edgtf-item-shown {
                    @include edgtfTransition(none);
                }
            }

            .touch & {
                opacity: 1;
                @include edgtfTransform(translateY(0));
            }
        }
    }

    /***** Additional Features - end *****/
}

/* ==========================================================================
   Stock Photography shortcode style - end
   ========================================================================== */

/* ==========================================================================
   Stock Photography filter style - begin
   ========================================================================== */

.edgtf-sp-filter-holder {
    @include edgtfRelativeHolderLayout();
    margin: 0 0 30px;
    text-align: center;

    ul {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin: 0;
        padding: 0;
        list-style: none;

        li {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            margin: 0 0 10px;
            padding: 0 22px;
            cursor: pointer;

            @include laptop-landscape-large {
                padding: 0 16px;
            }

            @include ipad-landscape {
                padding: 0 10px;
            }

            span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                color: #9c9c9c;
                white-space: nowrap;
                @include edgtfTransition(color .2s ease-out);
                font-size: 12px;
                line-height: 1.33em;
                letter-spacing: 0.08em;
                font-weight: 600;
                text-transform: uppercase;
            }

            &.edgtf-sp-current,
            &:hover {

                span {
                    color: $first-main-color;
                }
            }
        }
    }
}
/* ==========================================================================
   Stock Photography filter style - end
   ========================================================================== */

/* ==========================================================================
   Stock Photography standard pagination style - begin
   ========================================================================== */

.edgtf-stock-photography-list-holder {

    &.edgtf-sp-pag-standard {

        .edgtf-sp-inner {
            opacity: 1;
            @include edgtfTransition(opacity .2s ease-out);

            &.edgtf-no-elements {
                height: 150px !important;
            }
        }

        &.edgtf-sp-pag-standard-animate {

            .edgtf-sp-inner {
                opacity: 0;
            }
        }
    }
}

.edgtf-sp-standard-pagination {
    @include edgtfRelativeHolderLayout();
    margin: 20px 0 0;
    clear: both;

    ul {
        @include edgtfRelativeHolderLayout();
        padding: 0;
        margin: 0;
        list-style: none;
        text-align: center;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0 12px;
            padding: 0;

            a {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                margin: 0;
                padding: 0;
            }

            &.edgtf-sp-pag-active {

                a {
                    color: $first-main-color;
                }
            }

            &.edgtf-sp-pag-prev,
            &.edgtf-sp-pag-next {
                position: absolute;
                top: 0;

                a {
                    font-size: 30px;
                    @include edgtfTransition(color .2s ease-out, opacity .2s ease-out);

                    span {
                        display: block;
                        line-height: inherit;

                        &:before {
                            display: block;
                            line-height: inherit;
                        }
                    }
                }
            }

            &.edgtf-sp-pag-prev {
                left: 0;

                a {
                    opacity: 0;
                }
            }

            &.edgtf-sp-pag-next {
                right: 0;

                a {
                    opacity: 1;
                }
            }
        }
    }
}
/* ==========================================================================
   Stock Photography standard pagination style - end
   ========================================================================== */

/* ==========================================================================
   Stock Photography load more pagination style - begin
   ========================================================================== */

.edgtf-sp-load-more-holder {
    @include edgtfRelativeHolderLayout();

    .edgtf-sp-load-more {
        margin: 40px 0 0;
        text-align: center;
    }
}
/* ==========================================================================
   Stock Photography load more pagination style - end
   ========================================================================== */

/* ==========================================================================
   Stock Photography loading element style - begin
   ========================================================================== */

.edgtf-sp-loading {
    position: relative;
    display: none;
    width: 100%;
    margin: 30px 0 50px;
    color: $default-heading-color;
    text-align: center;

    &.edgtf-filter-trigger {
        position: absolute;
        top: 250px;
        left: 0;
    }

    &.edgtf-standard-pag-trigger {
        position: absolute;
        top: 50px;
        left: 0;

        .edgtf-sp-has-filter & {
            top: 150px;
        }
    }

    &.edgtf-showing {
        display: block;
    }

    > div {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 10px;
        height: 10px;
        margin: 0 5px;
        background-color: $default-heading-color;
        @include edgtfAnimation(sk-bouncedelay 1.4s infinite ease-in-out both);
    }

    .edgtf-sp-loading-bounce1 {
        -webkit-animation-delay: -0.32s;
        -moz-animation-delay: -0.32s;
        animation-delay: -0.32s;
    }

    .edgtf-sp-loading-bounce2 {
        -webkit-animation-delay: -0.16s;
        -moz-animation-delay: -0.16s;
        animation-delay: -0.16s;
    }
}

@-webkit-keyframes sk-bouncedelay {
    0%, 80%, 100% {
        -webkit-transform: scale(0);
    }
    40% {
        -webkit-transform: scale(1.0);
    }
}

@-moz-keyframes sk-bouncedelay {
    0%, 80%, 100% {
        -moz-transform: scale(0);
    }
    40% {
        -moz-transform: scale(1.0);
    }
}

@keyframes sk-bouncedelay {
    0%, 80%, 100% {
        -webkit-transform: scale(0);
        transform: scale(0);
    }
    40% {
        -webkit-transform: scale(1.0);
        transform: scale(1.0);
    }
}
/* ==========================================================================
   Stock Photography loading element style - end
   ========================================================================== */