/* ==========================================================================
   Stock Photography Single page style - begin
   ========================================================================== */

.edgtf-stock-photography-single-holder {
    @include edgtfRelativeHolderLayout();

    .edgtf-stock-photography-section-top {
        padding: 0 55px;
        box-sizing: border-box;
    }

    .edgtf-stock-photography-section-bottom {
        background-color: #f5f5f5;
        padding: 0 0 75px;
        .edgtf-container {
            background-color: transparent;
        }
    }

    .edgtf-sps-image-holder {
        @include edgtfRelativeHolderLayout();

        .edgtf-sps-image {
            @include edgtfRelativeHolderLayout();
            box-sizing: border-box;

            a, img {
                position: relative;
                display: block;
                margin: 0 auto;
            }
        }
    }

    .edgtf-sps-author-description {
        .edgtf-author-description-image {
            img {
                border-radius: 50%;
            }
        }
    }
}

/* ==========================================================================
   Stock Photography Single page style - end
   ========================================================================== */