/* ==========================================================================
   Proofing Gallery shortcode style - begin
   ========================================================================== */

.edgtf-proofing-gallery-list-holder {

	@include edgtfRelativeHolderLayout();

	/***** Article Global Style - begin *****/

    article {
	    @include edgtfRelativeHolderLayout();
        box-sizing: border-box;

	    .touch & {
		    cursor: pointer;
	    }

	    .edgtf-pgl-item-inner {
		    @include edgtfRelativeHolderLayout();
		    overflow: hidden;
	    }
	    
	    .edgtf-pgli-image {
		    @include edgtfRelativeHolderLayout();
		    
		    img {
			   vertical-align: middle;
		    }
	    }

	    .edgtf-pgli-link {
		    @include edgtfAbsoluteHolderLayout();
	    }

	    .edgtf-pgli-text-wrapper {
	        @include edgtfTableLayout();
	    }

	    .edgtf-pgli-text {
		    @include edgtfTableCellLayout();

		    .edgtf-pgli-title {
				margin: 0;
				padding: 13px 0 0;
		    }
	    }
    }

	/***** Article Global Style - end *****/
	
	/***** Proofing Gallery Types - begin *****/
	
	&.edgtf-pgl-gallery {
		
		&:not(.edgtf-pgl-one-column) {
			
			article {
				float: left;
			}
		}
		
		$gallery_columns_number: ('two', 'three', 'four', 'five');
		
		@for $i from 0 to length($gallery_columns_number) {
			&.edgtf-pgl-#{nth($gallery_columns_number,$i+1)}-columns {
				
				article {
					width: 100% / ($i+2);
				}
				
				@if ($i > 1) { // set different break point for four and five columns
					@media only screen and (min-width: $laptop-landscape-plus-pixel) {
						
						article {
							
							&:nth-child(#{$i+2}n+1) {
								clear: both;
							}
						}
					}
				} @else {
					@media only screen and (min-width: $ipad-landscape-plus-pixel) {
						
						article {
							
							&:nth-child(#{$i+2}n+1) {
								clear: both;
							}
						}
					}
				}
			}
		}
	}
	
	/***** Proofing Gallery Types - end *****/

	/***** Additional Features - begin *****/

	&.edgtf-pgl-has-animation {

		article {
			opacity: 0;
			@include edgtfTransform(translateY(80px));
			@include edgtfTransition(opacity .8s cubic-bezier(0.34, 0.52, 0.57, 1.04), transform .8s cubic-bezier(0.34, 0.52, 0.57, 1.04));

			&.edgtf-item-show {
				opacity: 1;
				@include edgtfTransform(translateY(0));

				&.edgtf-item-shown {
					@include edgtfTransition(none);
				}
			}
			
			.touch & {
				opacity: 1;
				@include edgtfTransform(translateY(0));
			}
		}
	}

	/***** Additional Features - end *****/
}

/* ==========================================================================
   Proofing Gallery shortcode style - end
   ========================================================================== */

/* ==========================================================================
   Proofing Gallery filter style - begin
   ========================================================================== */

.edgtf-pgl-filter-holder {
    @include edgtfRelativeHolderLayout();
    margin: 0 0 36px;

    ul {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin: 15px 0;
        padding: 0;
        list-style: none;

        li {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            cursor: pointer;
	        
	        @include laptop-landscape-large {
		        padding: 0 16px;
	        }
	
	        @include ipad-landscape {
		        padding: 0 10px;
	        }

            span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                padding: 0 15px;
                color: #a6a6a6;
				letter-spacing: 1px;
				text-transform: uppercase;
                @include edgtfTransition(color .2s ease-out);
            }
            &:first-child span{
				padding-left: 0
			}

            &.edgtf-pgl-current,
            &:hover {

                span {
                    color: $first-main-color;
                }
            }
        }
    }
}
/* ==========================================================================
   Proofing Gallery filter style - end
   ========================================================================== */

/* ==========================================================================
   Proofing Gallery standard pagination style - begin
   ========================================================================== */

.edgtf-proofing-gallery-list-holder {
	
	&.edgtf-pgl-pag-standard {
		
		.edgtf-pgl-inner {
			opacity: 1;
			@include edgtfTransition(opacity .2s ease-out);
		}
		
		&.edgtf-pgl-pag-standard-animate {
			
			.edgtf-pgl-inner {
				opacity: 0;
			}
		}
	}
}

.edgtf-pgl-standard-pagination {
	@include edgtfRelativeHolderLayout();
	margin: 20px 0 0;
	clear: both;
	
	ul {
		@include edgtfRelativeHolderLayout();
		padding: 0;
		margin: 0;
		list-style: none;
		text-align: center;
		
		li {
			position: relative;
			display: inline-block;
			vertical-align: top;
			margin: 0 12px;
			padding: 0;
			
			a {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				margin: 0;
				padding: 0;
			}
			
			&.edgtf-pgl-pag-active {
				
				a {
					color: $first-main-color;
				}
			}
			
			&.edgtf-pgl-pag-prev,
			&.edgtf-pgl-pag-next {
				position: absolute;
				top: 0;
				
				a {
					font-size: 30px;
					@include edgtfTransition(color .2s ease-out, opacity .2s ease-out);
					
					span {
						display: block;
						line-height: inherit;
						
						&:before {
							display: block;
							line-height: inherit;
						}
					}
				}
			}
			
			&.edgtf-pgl-pag-prev {
				left: 0;
				
				a {
					opacity: 0;
				}
			}
			
			&.edgtf-pgl-pag-next {
				right: 0;
				
				a {
					opacity: 1;
				}
			}
		}
	}
}
/* ==========================================================================
   Proofing Gallery standard pagination style - end
   ========================================================================== */

/* ==========================================================================
   Proofing Gallery load more pagination style - begin
   ========================================================================== */

.edgtf-pgl-load-more-holder {
	@include edgtfRelativeHolderLayout();

    .edgtf-pgl-load-more {
	    margin: 40px 0 0;
	    text-align: center;
    }
}
/* ==========================================================================
   Proofing Gallery load more pagination style - end
   ========================================================================== */

/* ==========================================================================
   Proofing Gallery loading element style - begin
   ========================================================================== */

.edgtf-pgl-loading {
	position: relative;
	display: none;
	width: 100%;
	margin: 40px 0 20px;
	color: $default-heading-color;
	text-align: center;

	&.edgtf-filter-trigger {
		position: absolute;
		top: 250px;
		left: 0;
	}
	
    &.edgtf-standard-pag-trigger {
		position: absolute;
		top: 50px;
		left: 0;
	
	    .edgtf-pgl-has-filter & {
		    top: 150px;
	    }
    }

	&.edgtf-showing {
		display: block;
	}

	> div {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		width: 14px;
		height: 14px;
		margin: 0 3px;
		background-color: $default-heading-color;
		border-radius: 100%;
		@include edgtfAnimation(sk-bouncedelay 1.4s infinite ease-in-out both);
	}

	.edgtf-pgl-loading-bounce1 {
		-webkit-animation-delay: -0.32s;
		-moz-animation-delay: -0.32s;
		animation-delay: -0.32s;
	}

	.edgtf-pgl-loading-bounce2 {
		-webkit-animation-delay: -0.16s;
		-moz-animation-delay: -0.16s;
		animation-delay: -0.16s;
	}
}

@-webkit-keyframes sk-bouncedelay {
	0%, 80%, 100% {
		-webkit-transform: scale(0);
	}
	40% {
		-webkit-transform: scale(1.0);
	}
}

@-moz-keyframes sk-bouncedelay {
	0%, 80%, 100% {
		-moz-transform: scale(0);
	}
	40% {
		-moz-transform: scale(1.0);
	}
}

@keyframes sk-bouncedelay {
	0%, 80%, 100% {
		-webkit-transform: scale(0);
		transform: scale(0);
	}
	40% {
		-webkit-transform: scale(1.0);
		transform: scale(1.0);
	}
}
/* ==========================================================================
   Proofing Gallery loading element style - end
   ========================================================================== */